import { getIn } from "formik";
import React from "react";
import { FormFeedback, FormGroup, Input, Label } from "reactstrap";

export const CheckBoxInput = ({
  field,
  form: { touched, errors },
  ...props
}) => {
  const invalid = getIn(touched, field.name) && getIn(errors, field.name);

  return (
    <>
      <Input
        type="checkbox"
        {...field}
        {...props}
        invalid={invalid}
        checked={field.value}
      />
      {invalid && (
        <FormFeedback className="d-block mt-0 mb-1">
          {getIn(errors, field.name)}
        </FormFeedback>
      )}
    </>
  );
};

export const CheckBoxLabeled = ({
  field,
  form: { touched, errors, setFieldValue },
  onChange = () => {},
  ...props
}) => {
  const invalid = getIn(touched, field.name) && getIn(errors, field.name);

  return (
    <FormGroup check {...props}>
      <Label check for={props.label}>
        <Input
          type="checkbox"
          {...field}
          {...props}
          invalid={invalid}
          checked={field.value}
          // defaultValue={false}
          onChange={(val) => {
            setFieldValue(field.name, val.target.checked);
            onChange();
          }}
          // defaultChecked={field.value}
        />
        {props.label}
      </Label>
      {invalid && (
        <FormFeedback className="d-block mt-0 mb-1">
          {getIn(errors, field.name)}
        </FormFeedback>
      )}
    </FormGroup>
  );
};

export const CheckBoxLabeledLazy = ({
  field,
  form: { touched, errors, setFieldValue },
  ...props
}) => {
  const invalid = getIn(touched, field.name) && getIn(errors, field.name);
  const handleOnChange = (e) => {
    const { name, onChange } = field;
    onChange(e);
    setTimeout(() => {
      setFieldValue(name, true);
    }, 0);
  };

  return (
    <FormGroup check {...props}>
      <Label check for={props.for || props.label}>
        <Input
          type="checkbox"
          {...field}
          {...props}
          invalid={invalid}
          checked={field.value}
          onChange={handleOnChange}
        />
        {props.label}
      </Label>
      {invalid && (
        <FormFeedback className="d-block mt-0 mb-1">
          {getIn(errors, field.name)}
        </FormFeedback>
      )}{" "}
    </FormGroup>
  );
};
